}

.r-r-button {
	margin: 5px;
	display: -webkit-inline-flex;
	display: inline-flex;
	cursor: pointer;
	-webkit-user-select: none;
	        user-select: none;
	-webkit-align-items: center;
	        align-items: center;
	vertical-align: bottom;
	text-align: center;
	overflow: hidden;
	position: relative;
	background-color: #F5F8FA;
	color: #293742;
	padding: 10px;
	border-radius: 5px;
	border: 1px solid #A7B6C2;
	text-decoration: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.r-r-button-icon {
    
}

.r-r-button-rounded-border {
    border-radius: 20px;
}

.r-r-button-raised-border {
    border: 1px solid #A7B6C2;
    box-shadow: 1px 2.5px 5px #A7B6C2;
    -webkit-box-shadow: 1px 2.5px 5px #A7B6C2;
    -moz-box-shadow: 1px 2.5px 5px #A7B6C2;
}

.r-r-button:hover {
    -webkit-filter: brightness(97%);
            filter: brightness(97%);
}

.r-r-button:active {
    box-shadow: inset 0px 0px 400px 110px rgba(0, 0, 0, .1);
}

.r-r-button:focus {
    z-index: 9;
    outline: none;
    box-shadow: 0 0 0 3px rgba(167, 182, 194, .5);
    -webkit-box-shadow: 0 0 0 3px rgba(167, 182, 194, .5);
    -moz-box-shadow: 0 0 0 3px rgba(167, 182, 194, .5);
}

/* ButtonGroup */

.r-r-button-group {
    display: -webkit-flex;
    display: flex;
    padding: 0px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.r-r-button-group > .r-r-button {
    margin: 0px;
    border-radius: 0px;
}

.r-r-button-group > .r-r-button:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.r-r-button-group > .r-r-button:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.r-r-button-group.r-r-flex-vertical {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.r-r-button-group.r-r-flex-vertical > .r-r-button {
    border-radius: 0px;
}

