/* Dialog */

.r-r-dialog, .r-r-dialog-modal.r-r-component-overlay {
	pointer-events: auto;
}

.r-r-dialog {
	display: -webkit-flex;
	display: flex;
	width: 50%;
	-webkit-flex-direction: column;
	        flex-direction: column;
	max-height: 90%;
	-webkit-transform: scale(1);
	transform: scale(1);
	position: relative;
}

.r-r-dialog-modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: none;
	pointer-events: none;
	background-color: transparent;
	transition-property: background-color;
