.r-r-dialog-right .r-r-dialog-enter,
.r-r-dialog-right .r-r-dialog-exit-active,
.r-r-dialog-top-right .r-r-dialog-enter,
.r-r-dialog-top-right .r-r-dialog-exit-active,
