.r-r-dialog-top .r-r-dialog-enter-active,
.r-r-dialog-bottom .r-r-dialog-enter-active,
.r-r-dialog-left .r-r-dialog-enter-active,
.r-r-dialog-top-left .r-r-dialog-enter-active,
