/* Popup */

.r-r-popover {
	--overlayArrowLeft: 0px;
	position: absolute;
	border: 0 none;
	margin-top: 13px;
	min-width: 50px;
	box-shadow: 0px 11px 15px -7px rgb(0 0 0 / 20%), 0px 24px 38px 3px rgb(0 0 0 / 14%), 0px 9px 46px 8px rgb(0 0 0 / 12%);
}

.r-r-popover-arrow:before {
	content: " ";
	display: block;
	position: absolute;
	left: calc(1px + 1.25rem);
	left: calc(var(--popoverArrowLeftOffset, 1px) + 1.25rem);
	top: -10px;
	border-bottom: 10px solid white;
	border-left: 10px solid transparent;
	border-right: 10px solid transparent;
	pointer-events: none;
}

.r-r-popover-arrow-flipped:before {
	top: 100%;
	border-bottom: 0px;
	border-top: 10px solid white;
}

.r-r-popover-enter {
	opacity: 0;
	-webkit-transform: scaleY(0.8);
	        transform: scaleY(0.8);
}

.r-r-popover-enter-active {
	opacity: 1;
	-webkit-transform: scaleY(1);
	        transform: scaleY(1);
	transition: opacity .12s cubic-bezier(0, 0, 0.2, 1), -webkit-transform .12s cubic-bezier(0, 0, 0.2, 1);
	transition: transform .12s cubic-bezier(0, 0, 0.2, 1), opacity .12s cubic-bezier(0, 0, 0.2, 1);
	transition: transform .12s cubic-bezier(0, 0, 0.2, 1), opacity .12s cubic-bezier(0, 0, 0.2, 1), -webkit-transform .12s cubic-bezier(0, 0, 0.2, 1);
}

.r-r-popover-enter-done {
	-webkit-transform: none;
	        transform: none;
}

.r-r-popover-exit {
	opacity: 1;
}

.r-r-popover-exit-active {
	opacity: 0;
	transition: opacity .1s linear;
}


